import React, { component } from "react";

export default class Gallery extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      extend: false,
      index: 0
    };
  }
  handleExtend = index => {
    this.setState({ extend: true, index });
  };
  handleCollapse = () => {
    this.setState({ extend: false, index: 0 });
  };
  handleNextImage = () => {
    this.setState(state => ({ index: this.state.index + 1 }));
  };
  handleLastImage = () => {
    this.setState(state => ({ index: this.state.index - 1 }));
  };
  componentDidUpdate = () => {
    console.log(this.state);
  };
  render() {
    if (this.state.extend) {
      return (
        <div>
          {this.state.index < this.props.images.length ? (
            <button onClick={this.handleNextImage}>Next</button>
          ) : null}
          <div className="large">
            <img
              src={"/static/gallery/" + this.props.images[this.state.index]}
            />
          </div>
          {this.state.index > 0 ? (
            <button onClick={this.handleLastImage}>Last</button>
          ) : null}
          <button onClick={this.handleCollapse}>X</button>
        </div>
      );
    } else {
      return (
        <div className="masonry">
          {this.props.images.map((image, index) => (
            <img
              onClick={() => this.handleExtend(index)}
              key={index}
              className="item"
              src={"/static/gallery/" + image}
            />
          ))}

          <style jsx>{`
            .masonry {
              column-count: 4;
              column-gap: 0.5em;
            }
            .item {
              background-color: #eee;
              margin: 0 0 0.5em;
              width: 100%;
            }
            img {
              display: inline-block;
            }
          `}</style>
        </div>
      );
    }
  }
}
